<template>
	<div class="all">
		<ul>
			<li><span style="letter-spacing: 3px;">报修类型 :</span> <span class="right">{{data.repairType}}</span></li>
			<li><span style="letter-spacing: 3px;">报修地址 :</span> <span class="right">{{data.repairAddress}}</span></li>
			<li><span>报修人电话 :</span> <span class="right">{{data.repairPhone}}</span></li>
			<li><span style="letter-spacing: 3px;">报修内容 :</span></li>
			<li><span style="font-size: 14px;color: #8e8e8e;;">{{data.repairContent}}</span></li>
			<li v-for="imgUrl in imageUrl">
				<img :src="imgUrl.filePath">
				
			</li>
		</ul>
		<button class="btn" type="button" @click="over">完工</button>
	</div>
	
</template>
<script>
	import {MessageBox} from 'mint-ui'
	import {mapState} from 'vuex'
	export default{
		data(){
			return{
				params:{
					repairId:this.$route.query.repairId
				},
				data:'',
				imageUrl:[],
				
			}
		},
		methods:{
			over(){
				MessageBox.alert('哈哈');
			},
			
			getRepairdetail(){
				this.$axios({
					url:'https://test.e-shequ.com/msa/crm/repairOder/getRepairOrderDetail?repairId='+this.params.repairId,
					method:'get'
				}).then((res) =>{
					this.data=res.data.result.repairOrder;
					this.imageUrl=res.data.result.list;
				})
			},
			updateDetail(){
				this.$axios({
					url:'https://test.e-shequ.com/msa/crm/repairOder/updateIsLookById?repairId='+this.params.repairId+'&userId='+this.userid,
					method:'get'
				}).then((res) =>{
					
				})
			}
		},
		mounted(){
			this.getRepairdetail();
			this.updateDetail();
		},
		computed:{
			...mapState({
				userid:state=>state.userid
			})
		}
		
		
		
	}
</script>

<style scoped>
.all{
	text-align: left;
	margin: 15px 3% 0;
	width: 94%;
	height: auto;
}	
.all ul li{
	margin-bottom:20px ;
	
}
.all ul li span:first-child{
	color: #000;
	font-size: 16px;
	
}
.all ul li:nth-child(4){
	margin-bottom: 10px;
}
.all ul li:nth-child(5){
	text-indent: 25px;
	line-height: 25px;
	margin:0 8px;
}
.all ul li:last-child{
	margin-top: 20px;
}
.all ul li:last-child img{
	float: left;
	width: 30%;
	margin-left: 5%;
}
.all ul li:last-child img:first-child{
	margin-left: 0;
}
.all .right{
	margin-left: 10px;
	font-size: 14px;
	color: #8e8e8e;
}
.btn{
	position: fixed;
	bottom: 10px;
	left: 5%;
	width: 90%;
	padding: 8px 0;
	background-color:#5e98d8 ;
	color: #fff;
	font-size: 18px;
	border-radius: 4px;
	outline: none;
	border: none;
}
</style>


